@media (min-width: 1200px)
    
    header 
        height: 80px
        // width: 100%
        display: flex
        align-items: center
        justify-content: space-between 

    .menu-links 
        display: flex
        gap: 40px
        cursor: pointer

    .menu-link 
        text-decoration: none
        font-style: normal
        font-weight: 500
        font-size: 16px

        color: #281B19

        transition: color 0.2s linear

        &:hover
            color: #63A6B9

